<template>
    <div 
    class="filter-item"
    :class="filter" 
    @click="fire"  
    :style="{ backgroundImage: `url(${UploadImageUrl})`}">
        <slot name="filterName"></slot>
    </div> 
</template>

<script>
import { mapMutations, mapState } from 'vuex';
    export default {
        name:"FilterBox",
        props: {
            uploadImageUrl: String,
            filter: String
        },
        computed: {
            ...mapState(['UploadImageUrl'])
        },
        methods: {
            ...mapMutations(['setMyfilter']),
            fire(){
                this.setMyfilter(this.filter);
            }
        }
    }
</script>

<style lang="postcss" scoped>
.filter-item {
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    background-position : center;
  }
</style>