<template>
    <div class="post">
        <div class="post-header">
            <div class="profile" :style="{ backgroundImage: `url(${InstaData.userImage})` }"></div>
            <span class="profile-name">{{InstaData.name}}</span>
        </div>

        <div class="post-body"  
            @click="likeAction(index)"
            :class="InstaData.filter" 
            :style="{ backgroundImage: `url(${InstaData.postImage})` }"></div>

        <div class="post-content">
            <p>{{ InstaData.likes }} Likes</p>
            <p><strong>{{InstaData.name}}</strong> {{ InstaData.content }}</p>
            <p class="date">{{ InstaData.date }}</p>
        </div>
    </div> 
</template>

<script>
import { mapMutations } from 'vuex';

    export default {
        name: 'MyPost',
        props: {
            InstaData: Object,
            index: Number
        },
        methods: {
            ...mapMutations(['likeAction'])
        }
    }
</script>

