<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="before">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="after">Next</li>
      <li v-if="step==2" @click="posting">글발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <MyContainer
   :step="step"
  />

  <button v-if="step == 0" @click="$store.dispatch('getData')">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="uploadImage" type="file" accept="image/*" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>
</template>

<script>
import { mapMutations } from 'vuex';
import MyContainer from './components/Container.vue'



export default {
  name: 'App',
  data(){
    return{
      step: 0,
    }
  },
  components: {
    MyContainer
  },

  methods: {
    ...mapMutations(['setUploadImageUrl','postCreate']),

    uploadImage(e){
      let file = e.target.files
      let url = URL.createObjectURL(file[0]);
      this.setUploadImageUrl(url); // ✅ 여기!
      this.step = 1;
    },
    posting(){
      this.postCreate();
      this.step=0;
    },

    before(){
      if(this.step == 0){
        return;
      }
      this.step--;
    },
    after(){
      if(this.step == 4){
        return;
      }
      this.step++;
    },

  },

}
</script>

