import { createStore } from 'vuex'
import axios from 'axios'
import InstaData from './assets/data'

const store = createStore({
  state(){
    return {
            InstaDatas: [...InstaData],
            UploadImageUrl: '',
            Content: '',
            Myfilter: '',
        }
  },

  mutations: {
    setMore(state,data){
        state.InstaDatas.push(data);
    },
    setUploadImageUrl(state,data){
        state.UploadImageUrl = data;
    },
    setContent(state,data){
        state.Content = data;
    },
    setMyfilter(state,data){
        state.Myfilter = data;
    },
    likeAction(state, index) {
        if (state.InstaDatas[index].liked) {
          state.InstaDatas[index].likes--;
        } else {
          state.InstaDatas[index].likes++;
        }
        state.InstaDatas[index].liked = !state.InstaDatas[index].liked; // 좋아요 상태도 토글
    },

    postCreate(state){
        let data = {
            name: "junsupark",
            userImage: "https://picsum.photos/100?random=3",
            postImage: state.UploadImageUrl,
            likes: 56,
            date: "방금전",
            liked: false,
            content: state.Content,
            filter: state.Myfilter
        }
        state.InstaDatas.unshift(data);

    },
  },
  actions: {
    getData(context){
        axios.get(`https://codingapple1.github.io/vue/more1.json`)
        .then((response) => {
            console.log(response.data)
            context.commit('setMore', response.data)
      })
    }
  }
})

export default store