<template>
    <div style="padding : 10px">
      <h4>팔로워</h4>
      <input v-model="keyword" placeholder="검색어 입력" />

      <!-- 수정 (✅ 검색어에 따라 필터링된 리스트 보여줌) -->
<div class="post-header" v-for="follower in filteredFollowers" :key="follower.id">

        <div class="profile" :style="{ backgroundImage: `url(${follower.image})` }"></div>
        <span class="profile-name">{{ follower.name }}</span>
      </div>
    </div>
    </template>

<script>
    import axios from 'axios';
import { onMounted, ref,computed } from 'vue';
    export default {
        name : 'MyPage',

        setup(){


            const followers = ref([])               // 전체 팔로워 리스트
            const keyword = ref('')                 // 검색어 입력값

            // 검색어 기반 필터링된 팔로워 리스트
            const filteredFollowers = computed(() => {
            return followers.value.filter(f =>
                f.name.toLowerCase().includes(keyword.value.toLowerCase())
            )
            })
            onMounted(()=>{
                axios.get('/follower.json').then((a)=>{
                followers.value = a.data;
                })
            })

            

                        return {
                followers,
                keyword,
                filteredFollowers
                }
        },
        data(){
            return{

            }
        }
    }
</script>

