<template>
    <div>
        <!-- post -->
        <div v-if="step === 0">
            <MyPost 
            v-for="(InstaData,index) in $store.state.InstaDatas" :key="InstaData"
            :InstaData="InstaData"
            :index="index" />
        </div>
        


        <!-- 필터선택페이지 -->
         <div v-if="step === 1">
            <div class="upload-image" 
                :class="Myfilter" 
                :style="{ backgroundImage: `url(${UploadImageUrl})`  }"></div>
            <div class="filters">
                <FilterBox 
                    v-for="filter in filters" :key="filter"
                    :filter="filter"
                    >
                    <template v-slot:filterName>{{ filter }}</template>
                </FilterBox>
            </div>
         </div>
        
        <div v-if="step === 2">
            <!-- 글작성페이지 -->
            <div class="upload-image" 
                :class="Myfilter" 
            :style="{ backgroundImage: `url(${UploadImageUrl})`  }"></div>
            <div class="write">
                <textarea class="write-box" @input="updateContent"></textarea>
            </div>
        </div>

        <div v-if="step === 3">
            <MyPage :one="1"/>
        </div>
        

    </div>
</template>

<script>
    import MyPost from './Post.vue'
    import FilterBox from './FilterBox.vue'
    import MyPage from './MyPage.vue'
    import { mapMutations, mapState } from 'vuex';

    export default {
        name: 'MyConrainer',
        components: {
            MyPost,
            FilterBox,
            MyPage
        },
        data() {
            return{
                filters: [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                            "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                            "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            }
        },
        mounted(){
            this.emitter.on('MyFilter', (filter) => {
                this.Myfilter = filter;
            });
        },
        props: {
            step: Number,
        },
        computed: {
            ...mapState(['UploadImageUrl', 'Myfilter'])
        },
        methods: {
            ...mapMutations(['setContent']),
            updateContent(e) {
                this.setContent(e.target.value);
            }
        },

    }
</script>

<style lang="postcss" scoped>
.upload-image{
    width: 100%;
    height: 450px;
    background: cornflowerblue;
    background-size : cover;
    }
    .filters{
    overflow-x:scroll;
    white-space: nowrap;
    }
    .filter-1 {
    width: 100px;
    height: 100px;
    background-color: cornflowerblue;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    }
    .filters::-webkit-scrollbar {
    height: 5px;
    }
    .filters::-webkit-scrollbar-track {
    background: #f1f1f1; 
    }
    .filters::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 5px;
    }
    .filters::-webkit-scrollbar-thumb:hover {
    background: #555; 
    }
    .write-box {
    border: none;
    width: 90%;
    height: 100px;
    padding: 15px;
    margin: auto;
    display: block;
    outline: none;
    }
</style>